import React from "react";
import { SimpleCell, Button, Div, Link } from "@vkontakte/vkui";

const GameItem = ({ id, go, nameGame, icon,namePanelGame }) => {
  return (
    <SimpleCell
      before={
        <Div id={id} onClick={go} data-to={"home"}>
          {icon}
        </Div>
      }
      // after={
      //   <Link id={id} mode="tertiary">
      //     –∫—É–ø–∏—Ç—å
      //   </Link>
      // }
      description={
        <>
          <Div
            style={{
              display: "flex",
              alignItems: "center",
              flexDirection: "column",
              justifyContent: "start",
              marginTop: 5,
              marginBottom: 5,
              padding: 0,
            }}
          >
            {/* <Div>–ò–≥—Ä–∞ –†—É–ª–µ—Ç–∫–∞</Div> */}
            <Button
              size="m"
              onClick={go}
              data-to={namePanelGame}
              style={{
                backgroundColor: "green",
                color: "white",
                borderRadius: "6px",
                width: "120px",
              }}
            >
              –ò–≥—Ä–∞—Ç—å
            </Button>
          </Div>
        </>
      }
    >
      {nameGame}
    </SimpleCell>
  );
};

export { GameItem };
